import Link from "next/link";
import { ButtonLink } from "@/components/ui/Button";
import { Marquee } from "@/components/ui/Marquee";
import { callToAction, contactMarqueeItems } from "@/lib/homeData";

export function CallToAction() {
  return (
    <section className="bg-white">
      <div className="border-y border-zinc-900/10 bg-zinc-50 py-6">
        <Marquee
          items={contactMarqueeItems}
          separator="·"
          className="px-4 sm:px-6"
          textClassName="text-xs font-semibold uppercase tracking-[0.4em] text-zinc-600"
          speed="normal"
        />
      </div>

      <div className="mx-auto max-w-4xl px-4 py-24 text-center sm:px-6">
        <div className="space-y-6">
          <span className="inline-flex rounded-full border border-zinc-900/10 px-6 py-2 text-xs font-semibold uppercase tracking-[0.4em] text-zinc-600">
            {callToAction.eyebrow}
          </span>
          <p className="mx-auto max-w-2xl text-lg leading-relaxed text-zinc-700 md:text-xl">
            {callToAction.message}
          </p>
          <div className="space-y-4">
            <p className="text-base text-zinc-600">
              Get in touch with me at{" "}
              <a
                href={`mailto:${callToAction.email}`}
                className="font-semibold text-[#EE73DE] hover:underline"
              >
                {callToAction.email}
              </a>
            </p>
            <div className="flex items-center justify-center gap-6 pt-2">
              {callToAction.socialLinks.map((social) => (
                <Link
                  key={social.name}
                  href={social.href}
                  target="_blank"
                  rel="noreferrer"
                  className="text-sm font-semibold uppercase tracking-[0.3em] text-zinc-700 transition hover:text-[#EE73DE]"
                >
                  {social.name}
                </Link>
              ))}
            </div>
          </div>
          <ButtonLink href={callToAction.buttonHref} variant="secondary">
            {callToAction.buttonLabel}
          </ButtonLink>
        </div>
      </div>
    </section>
  );
}



